import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter, Switch } from "react-router-dom";
import { Provider } from "react-redux";

import { store } from "_helpers";
import { routes } from "data/routes";
import { fetchEntriesRequest } from "store/entries/actions";
import { getUsers } from "store/users/actions";

import "assets/css/index.scss";
import LoginLayout from "layouts/LoginLayout";
import MainLayout from "layouts/MainLayout";
import { AppRoute } from "components/UI/AppRoute";

import { PrivateRoute } from "components/UI/PrivateRoute";

import Activities from "components/Activities";
import Login from "components/Login";
import Users from "components/Users";
import UsersOld from "components/UsersOld";
import Entries from "components/Entries";
import EditEntry from "components/Entry/EditEntry";
import AddEntry from "components/Entry/AddEntry";
import Settings from "components/Settings";
import Profile from "components/Profile";

import * as serviceWorker from "./serviceWorker";

// setup fake backend
import { configureFakeBackend } from "./_helpers/fake-backend";
configureFakeBackend();

ReactDOM.render(
  <Provider store={store}>
    <BrowserRouter>
      <Switch>
        <AppRoute
          exact
          path={routes.Home}
          component={Activities}
          layout={MainLayout}
        />
        <AppRoute
          exact
          path={routes.Users}
          component={Users}
          layout={MainLayout}
        />
        <AppRoute
          exact
          path={routes.UsersOld}
          component={UsersOld}
          layout={MainLayout}
        />
        <PrivateRoute
          exact
          path={routes.Entries}
          component={Entries}
          layout={MainLayout}
        />
        <PrivateRoute
          exact
          path={routes.AddEntry}
          component={AddEntry}
          layout={MainLayout}
        />
        <PrivateRoute
          exact
          path={routes.EditEntry}
          component={EditEntry}
          layout={MainLayout}
        />
        <PrivateRoute
          exact
          path={routes.Settings}
          component={Settings}
          layout={MainLayout}
        />
        <PrivateRoute
          exact
          path={routes.Profile}
          component={Profile}
          layout={MainLayout}
        />
        <AppRoute
          exact
          path={routes.Login}
          component={Login}
          layout={LoginLayout}
        />
      </Switch>
    </BrowserRouter>
  </Provider>,
  document.getElementById("root")
);

fetchEntriesRequest();
getUsers();

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
