import React, { useState, useEffect } from "react";
import { IEntry } from "data/entry";
import { sideviewIcons } from "data/featherIcons";
import { useTypedDispatch } from "hooks/useTypedDispatch";

import { editEntry } from "store/entries/actions";
import { removeEntry } from "store/entries/actions";
import { useFormFields } from "hooks/useFormFields";
import { IUser } from "data/user";

const InitialState: IUser = {
  id: 1,
  name: "Loading...",
  username: "no username found",
  password: "no password found",
  job: "Sales",
  location: "Leeuwarden",
  role: "Admin"
}

interface Iprops {
  selected: IUser;
  setSelected: any;
  setDeleting: any;
}

export const SideView: React.FunctionComponent<Iprops> = (props) => {
  const { id, name, username, password, job, location, role } = props.selected ? props.selected : InitialState;
  const { selected, setSelected, setDeleting } = props;

  const dispatch = useTypedDispatch();
  const [fields, handleFieldChange, setForm] = useFormFields(selected ? selected : InitialState);
  const [editMode, setEditMode] = useState(false);
  const [editIcon, deleteIcon] = sideviewIcons;
  const [lastEdit, setLastEdit] = useState("Last edit at ")

  const tabs = ["Colleague", "Coached by", "Notes"];
  const [tabMode, setTabMode] = useState(0);

  useEffect(() => {
    setEditMode(false);
    setForm(selected);
  }, [selected]);

  useEffect(() => {
    if (editMode) {
      let timer = setTimeout(() => {
        dispatch(editEntry(fields))
      }, 1000);
      return () => {
        clearTimeout(timer);
        setSelected(selected);
      };
    }
  }, [fields.title, fields.body]);

  const deleteItem = () => {
    setDeleting(true);
    dispatch(removeEntry(id));
  };

  return (
    <div>
      <div className="sideview-functions">
        <div className="item">{lastEdit}</div>
        <div
          className={editMode ? "item edit active" : "item edit"}
          onClick={() => setEditMode(!editMode)}
        >
          {editIcon}
        </div>
        <div className="item delete" onClick={deleteItem}>
          {deleteIcon}
        </div>
      </div>
      <div className="sideview-item">
        <div className="sideview-item-menus">
          {/* Show all tabs and set selected class  */}
          <div className="tabs">
            {tabs.map((tab, index) => (
              <div
                key={index}
                className={index === tabMode ? "tab selected" : "tab"}
              >
                <a onClick={() => setTabMode(index)}>{tab}</a>
              </div>
            ))}
          </div>
          {editMode ?
            <div className="complete">
              <input type="submit" value="Save" />
            </div>
            :
            null
          }
        </div>
        <div className="sideview-content">
          <div className="sideview-content-title">
            <div className="avatar"><a>ML</a></div>
            <input
              id="name"
              placeholder="Add title..."
              value={fields.name}
              onChange={handleFieldChange}
              disabled={!editMode}
            />
          </div>
          <div className="sideview-content-item">
            <label htmlFor="email">Email</label>
            <input
              id="email"
              placeholder="Email..."
              value={fields.email}
              onChange={handleFieldChange}
              disabled={!editMode}
            />
          </div>
          <div className="sideview-content-item">
            <label htmlFor="job">Job</label>
            <input
              id="job"
              placeholder="Job..."
              value={fields.job}
              onChange={handleFieldChange}
              disabled={!editMode}
            />
          </div>
          <div className="sideview-content-item">
            <label htmlFor="location">Location</label>
            <input
              id="location"
              placeholder="Location..."
              value={fields.location}
              onChange={handleFieldChange}
              disabled={!editMode}
            />
          </div>
          <div className="sideview-content-item">
            <label htmlFor="role">Role</label>
            <input
              id="role"
              placeholder="Role..."
              value={fields.role}
              onChange={handleFieldChange}
              disabled={!editMode}
            />
          </div>
          <div className="sideview-content-item">
            <label htmlFor="coached-by">Coached By</label>
            <div id="coached-by">
              <div className="tag">Henk Holtrop</div>
              <div className="tag">Lennart Alserda</div>
            </div>
          </div>
          <div className="sideview-content-item">
            <label htmlFor="coached-by">Assigned to</label>
            <div id="coached-by">
              <div className="tag">React.Js</div>
              <div className="tag">Jira</div>
              <div className="tag">Alserda</div>
            </div>
          </div>
          <div className="sideview-content-item">
            <label htmlFor="coached-by">Completed</label>
            <div id="coached-by">
              <div className="tag">Drupal</div>
              <div className="tag">Angular</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SideView;
